import React from 'react';
import HomePage from './pages/HomePage';
import Store from './Store';
import { db } from './utils';

export default () => {
    const initialState = {
        categories: db.categories.get() || [],
        sites: {},
        options: db.options.get()
    };
    initialState.categories.map(category => initialState.sites[category] = db.sites.get(category));
    
    const reducer = (state = initialState, action) => {
        switch (action.type) {
            case 'ADD_CATEGORY': {
                if (state.categories.includes(action.data)) return;

                return {
                    ...state,
                    categories: [...state.categories, state.data]
                };
            }
            default:
            return state;
        }
    };

    return (
        <Store initialState={initialState} reducer={reducer}>
            HEYYY APP!
        </Store>
    );
};
